import router from '@/router/index'

export default {
	data() {
		return {
			data: [],
			account:[],
			mainDept:[]
		}
	},
	created() {
		this.getInfo();
	},
	methods: {
		getInfo() {
			var url = _baseUrl + '/mobile/oa/personalInfo/get.do';
			this.$ajax.get(url).then(response => {
				this.data = response.data.user;
				this.account = response.data.account[0];
				this.mainDept = response.data.mainDept == null? {'name':''} : response.data.mainDept;
			})
		},
		editInfo() {
			var userInfo = {
				fullname: this.data.fullname,
				userNo: this.data.userNo,
				mobile: this.data.mobile,
				email: this.data.email,
				address: this.data.address,
				qq: this.data.qq
			};
			var url = _baseUrl + "/mobile/oa/personalInfo/edit.do";
			this.$ajax({
					url: url,
					method: 'post',
					params: {
						userInfo: userInfo
					}
				}).then(response => {
					var rtn = response.data;
					if(rtn.success) {
						this.$dialog.toast({
							mes: rtn.message,
							timeout: 1500,
							icon: 'success'
						});
					}
				})
				.catch(error => {
					console.log(error);
				});
		},
		cancel() {
			router.go(-1);
		}
	}
}



